#modal-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background: rgba(0, 0, 0, 0.5);
}

#modal {
    position: absolute;
    left: 30%;
    width: 40%;
    top: 35%;
    display: flex;
    flex-direction: column;
    background-color: white;
    padding: 20px;
}

#modal-input {
    margin: 20px;
    margin-left: 0;
}

#modal-button-area {
    display: flex;
    flex-direction: row;
}

#modal-cancel {
    background-color: rgb(200, 200, 200);
    color: black;
    border-radius: 7px;
    margin: 0 15px;
    height: 36px;
    margin-left: 0;
}

#modal-submit {
    background-color: #558fed;
    color: white;
    border-radius: 7px;
    margin: 0 15px;
    height: 36px;
    min-width: 56px;
}