#homescreen-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    padding-top: 1.5vh;
    padding-bottom: 5vh;
    padding-left: 20vw;
    padding-right: 20vw;
    align-items: center;
}

#recording-display {
    width: 100%;
    height: 70vh;
    margin-bottom: 15px;
    overflow-y: scroll;
}

.paragraph {
    margin-bottom: 7px;
}

.indented {
    text-indent: 30px;
}

.button {
    background-color: #558fed;
    color: white;
    border-radius: 7px;
    text-align: center;
    width: 128px;
    margin-bottom: 15px;
    margin-left: 15px;
}

h5 {
    margin: 0;
}

#current-paragraph {
    margin: 15px;
}

.button-duo {
    display: flex;
}

#page-display {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 100%;
}

.arrow {
    margin: 24px;
    width: 48px;
}

#main-display {
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#toolbox {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-items: center;
    padding: 24px;
    padding-top: 32px;
    align-items: center;
    width: 348px;
    bottom: 0;
    right: 24px;
    background-color: white;
    -webkit-box-shadow: -3px 6px 16px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -3px 6px 16px -1px rgba(0, 0, 0, 0.75);
    box-shadow: -3px 6px 16px -1px rgba(0, 0, 0, 0.75);
    transition: all 0.6s linear;
    height: 35vh;
    box-sizing: border-box;
}

#toolbox-button {
    background-color: white;
    border-radius: 50px;
    position: absolute;
    width: 48px;
    height: 48px;
    top: -24px;
    left: 150px;
    -webkit-box-shadow: 1px 3px 26px -1px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 1px 3px 26px -1px rgba(0, 0, 0, 0.75);
    box-shadow: 1px 3px 26px -1px rgba(0, 0, 0, 0.75);
}

#toolbox-button-icon {
    width: 48px;
    height: 48px;
}